*{
    padding: 0 0 0 0;
    margin: 0 0 0 0;
    font-family: 'Inter', sans-serif;
}

h1 {
    padding-bottom: 30px;
    padding-top: 60px;
    text-align: center;
    font-size: 3rem;
    font-weight: bolder;
}

h2 {
    font-weight: bold;
    font-size: x-large;
}

h3 {
    font-size: large;
}

p {
    font-weight: bold;
    font-size: small;
}

#placeholder-appetizers {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 3em;
}

#placeholder-udon {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 3em;
    padding-bottom: 70px;
}

.container{
    background: #fbf9f2;
    display: grid;
    grid-auto-rows: 1fr 0.7fr;
    box-shadow: 0 0 5px rgb(0 0 0 / 50%);
}

.images {
    display: block;
    background-size: cover;
    object-fit: cover;
    height: 285px;
    width: 100%;
}


.content {
    display: grid;
    padding: 15px;
}
