p {
    font-family: "Inter", Arial;
}

.rewards-container {
    display: flex;
    flex-direction: row;
    max-height: 100%;
    max-width: 100%;
}

.left-container {
    min-height: 100%;
    width: 45%;
    background-color: #FBF9F2;
    padding-left: 7%;
    padding-top: 10%;
}

.noodles {
    font-size: 25px;
    font-weight: 600;
}

.slogan {
    font-size: 34px;
    font-weight: 700;
    border-bottom: 4px solid #FB8D05;
    margin-right: 35%;
    padding-bottom: 30px;
}

.rewards-content {
    font-size: 16px;
    font-weight: 600;
    margin-right: 25%;
    margin-bottom: 45px;
}

a {
    font-family: "Inter", Arial;
    color: black;
    font-weight: 600;
}

.rewards-container > img {
    width: 55%;
    max-height: 100%;
}