/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap'); */

.body-container {
    position: absolute;
    z-index: 5;
    margin-left: 10vw;
    margin-right: 10vw;
}

.orderNow-modal-container {
    margin: 0 auto;
}

.orderNow-modal {
    display: flex;
    flex-direction: row;
    width: 80vw;
    max-width: 100%;
    position: relative;
    background-color: #fbf9f2;
    box-shadow: 10px 10px 30px #252e41;
}

.BackgroundNoodle img {
    height: 100vh;
}

.BackgroundNoodle {
    display: flex;
}


.OrderInfo {
    margin: 15% 0;
}

.closeBtnDiv {
    position: absolute;
    top: 0;
    right: 0;
    padding-top: 10px;
    padding-right: 15px;
}

.closeBtn {
    border: none;
    background-color:#fbf9f2;
    font-size: 25px;
}

.RightColmn {
    width: 45%;
}

.LeftColmn {
    width: 55%;
}


.CloseBtn {
    margin: 0px;
    font-size: 24px;
    color: #252e41;
    cursor: default;
}


.BackgroundNoodle {
    display: flex;
}

.button {
    background-color: #ff9862;
    width: 35%;
    height: 70px;
    margin: 6px;
    padding: 10px;
    font-size: 23px;
    color: #252e41;
    border: none;
    border-radius: 35px;
}
.OrderBtns {
    display: flex;
    justify-content: center;
    position: relative;
    padding: 10px;
    background-color: #fbf9f2;
}

.LocationInfo {
    display: grid;
    grid-row: 1 / span 2;
    padding: 10px;
    margin: 60px 0px 0px 70px;
    font-size: 23px;
    color: #252e41;
}

label {
    cursor: default;
}

.LocationInfo input {
    margin: 10px 0;
    width: 90%;
    padding: 15px 0;
    background: transparent;
    border: none;
    border-bottom: 1px solid #252e41;
}


#CityStateZip {
    outline: none;
}