

section.signup.column {
  margin: 10px;
  margin-left: -10px;
}

section.login.column {
  margin: 10px;
}

.loginModal > a {position: relative;
left: 920px;
top:5px
}

h1 {
  font-family: "Inter", sans-serif;
}

p, a {
  font-family: "Inter", sans-serif;
}

button {
  font-family: "Inter", sans-serif;
}


.row {
  display: flex;
  flex-direction: row;
}

.column {
  display: flex;
  flex-direction: column;
}

.logos {
  justify-content: space-evenly;
  width: 120px;
}

.logos button {
  background-color: #fbf9f2;
  border: 0;
}

.logo {
  width: 30px;
}

.login {
  align-items: center;
  background-color: #fbf9f2;
  padding: 40px;
}

.login h1 {
  color: #23314e;
  font-size: 30px;
}

.login form {
  margin-top: 10vh;
  width: 35vw;
}

.login label {
  margin-bottom: 2vh;
  font-family: "Inter", sans-serif;
  color: #23314e;
}

.loginModal {
  width: 1200px;
  display: flex;
  align-items: stretch;
  justify-content: center;
  z-index: 3;
  position: absolute;
  
}

.login input {
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-color: lightgray;
  background-color: #fbf9f2;
  margin-bottom: 4vh;
}

.login-button {
  width: 35vw;
  background-color: lightgray;
  color: rgb(99, 95, 95);
  border: 0;
  font-size: 18px;
  padding: 15px;
  font-weight: bold;
  border-radius: 100px;
  margin-top: 10vh;
}

.options {
  align-items: center;
}

.options a {
  color: #23314e;
  font-weight: bold;
}

.options p {
  margin-left: 15px;
  margin-right: 5px;
}

.signup {
  align-items: center;
  background-color: #23314e;
  color: white;
  padding: 40px;

}

.signup h1 {
  font-size: 30px;
}

.signup p {
  font-size: 20px;
}

.signup button {
  background-color: rgb(228, 140, 76);
  color: #23314e;
  border: 0;
  padding: 15px;
  font-weight: bold;
  font-size: 18px;
  border-radius: 100px;
  width: 15vw;
  margin-top: 25%;

}


.background {
  display: flex;
  align-items: center;
  justify-content: center;
}

.background.hidden{
  visibility: hidden;
}
.a {
  position: absolute;
  left: 1200px;
 
}