* {
    padding: 0 0 0 0;
    margin: 0 0 0 0;
    font-family: 'Inter', sans-serif;
}

nav {
    display: flex;
    align-items: center;
    border-top: 5px solid darkorange;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.2);
    padding: 5px 15px 5px 15px;
    background-color: #fbf9f2;
    height: 50px;
}

.nav-logo {
    margin-right: 4%;
}

.nav-logo img {
    height: 100%;
}

.nav-links-container {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    gap: 10%;
}

.nav-entry {
    flex: 1;
    display: flex;
}

.nav-right {
    margin-left: auto;
    white-space: nowrap;
}

.nav-login-btn{
    background-color: #23314e;
    border-radius: 20px;
    border: none;
    color: rgb(228, 140, 76);
    padding: 10px 25px;
    font-size: 14px;
}

.nav-order-btn {
    background-color: darkorange;
    padding: 10px;
    border-radius: 20px;
    border: none;
    align-self: center;
    margin-left: 15px;
}

.active-nav-link {
    font-family: 'Inter', sans-serif;;
    color: black;
    font-weight: 425;
    text-decoration: none;
    border-bottom: 5px solid darkorange;
    cursor:pointer;
}

.nav-link{
    font-family: 'Inter', sans-serif;;
    color: black;
    font-weight: 425;
    text-decoration: none;
    cursor:pointer;
}

/* hamburger menu for mobile */
@media screen and (max-width: 710px) {
    .nav-menu-icon {
        position: relative;
        display: flex;
        flex: 1;
        justify-content: end;
        align-items: center;
        box-sizing: border-box;
        transition: all 0.5s ease-in-out;
        cursor: pointer;
    }

    .nav-menu-burger-icon {
        width: 35px;
        height: 5px;
        background: darkorange;
        border-radius: 5px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
        transition: all 0.2s ease-in-out;
    }

    .nav-menu-burger-icon::before,
    .nav-menu-burger-icon::after {
        content: "";
        position: absolute;
        width: 35px;
        height: 5px;
        background: darkorange;
        border-radius: 5px;
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
        transition: all 0.2s ease-in-out;
    }

    .nav-menu-burger-icon::before {
        transform: translateY(-10px);
    }

    .nav-menu-burger-icon::after {
        transform: translateY(10px);
    }

    .nav-links-container {
        display: none;
    }

    #nav-links-hamburger-container div {
        margin: 2%;
      }
    
      .nav-menu-icon.open .nav-menu-burger-icon {
        transform: translateX(-50px);
        background: transparent;
        box-shadow: none;
      }
    
      .nav-menu-icon.open .nav-menu-burger-icon::before {
        transform: rotate(45deg) translate(35px, -35px);
      }
    
      .nav-menu-icon.open .nav-menu-burger-icon::after {
        transform: rotate(-45deg) translate(35px, 35px);
      }

    .nav-entry {
        display: none;
    }

    #nav-entry {
        display: none;
      }
    
      #nav-order-btn-dropdown {
        background-color: darkorange;
        padding: 10px;
        border-radius: 20px;
        border: none;
        align-self: center;
      }
    
      #nav-links-container-dropdown {
        background-color: #fbf9f2;
        padding: 5px 0 5% 0;
        text-align: center;
      }
}